<template>
  <div class="join-room container">
    <h2>請輸入房號</h2>
    <input v-focus type="text" v-model.trim="RoomId" @keyup.enter="push()" />
    <b-button @click="push(RoomId)" id='JoinRoomConfirm' class="btn btn-info mt-3">確認</b-button>
    <b-button @click="back()" class="btn btn-info mt-3">返回</b-button>
  </div>
</template>

<script>
import router from '@/router'
import { db } from '@/db'
import _ from 'lodash'

export default {
  name: 'JoinRoom',
  data: () => {
    return {
      RoomId: null
    }
  },
  methods: {
    push: _.debounce(
      function () {
        const loading = this.$loading({
          target: '#JoinRoomConfirm'
        })
        const id = this.RoomId
        if (id >= 1000) {
          db.doc(id).get().then(doc => {
            if (doc.data()) {
              router.replace({ path: `/room/${id}` })
            } else {
              loading.close()
              this.$message.error({ message: '無此房號!! 請重新輸入', center: true, customClass: 'message-width' })
              this.RoomId = ''
            }
          })
        } else {
          loading.close()
          this.$message.error({ message: '請輸入正確的房號', center: true, customClass: 'message-width' })
        }
      }, 300),
    back: () => {
      router.replace({ path: '/' })
    }
  }
}
</script>

<style lang="scss" scoped>
.join-room {
  justify-content: center;
}

input {
  text-align: center;
  font-size: 24px;
  height: 54px;
}
</style>
